import 'rxjs/add/observable/fromEvent';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/takeUntil';
import { Observable } from 'rxjs/Observable';
import { fromPromise } from 'rxjs/observable/fromPromise';
import { Observer } from 'rxjs/Observer';
import { setTimeout } from 'timers';

export async function observableCharacteristic(characteristic: BluetoothRemoteGATTCharacteristic) {
  await characteristic.startNotifications();
  const disconnected = Observable.fromEvent(characteristic.service !.device, 'gattserverdisconnected');
  return Observable
    .fromEvent(characteristic, 'characteristicvaluechanged')
    .takeUntil(disconnected)
    .map((event: Event) => (event.target as BluetoothRemoteGATTCharacteristic).value as DataView);
}

export function getExclamationMarks(numChars: number) {
  return Array(numChars + 1).join(String.fromCharCode(10071));
}

export function experiments() {
  global
    .console
    .log('experiments');
  const observable = Observable.create((observer: Observer < string >) => {
    observer.next('hello');
    observer.next('world');
  });
  observable.subscribe((val: string) => {
    global
      .console
      .log(val);
  });

  const promise = new Promise((resolve, reject) => {
    setTimeout(() => {
      resolve('setTimeout resolved!');
    },         2000);
  });
  const obsProm = fromPromise(promise);
  obsProm.subscribe((val: string) => {
    global
      .console
      .log(val);
  });
}



