// const SYLVAC_SERVICE = 'c1b25000-caaf-6d0e-4c33-7dae30052840';

import { Observable } from 'rxjs/Observable';
import { observableCharacteristic } from './helpers';

export class SylvacClient {

    public deviceName: string | null = '';
    public valueReading: Observable < string >;
    private gatt: BluetoothRemoteGATTServer | null = null;

    public handleBLECharacteristics(event: Event): void {
        global
            .console
            .log(event);
        const value = (event.target as BluetoothRemoteGATTCharacteristic).value;
        const decoder = new TextDecoder('utf-8');
        const decodedString = decoder.decode(value);
        global
            .console
            .log(decodedString);
    }

    public async connect(gatt?: BluetoothRemoteGATTServer) {
        if (gatt) {
            this.gatt = gatt;
        } else {
            const device = await navigator
                .bluetooth
                .requestDevice({acceptAllDevices: true, optionalServices: ['c1b25000-caaf-6d0e-4c33-7dae30052840']});
            this.gatt = await device.gatt !.connect();
        }
        this.deviceName = this.gatt.device.name || null;
        global
            .console
            .log('Device ' + this.deviceName + ' connected');

        const service = await this
            .gatt
            .getPrimaryService('c1b25000-caaf-6d0e-4c33-7dae30052840');
        global
            .console
            .log('Got primary service ', service);

        const indicationCharacteristics = await service.getCharacteristic('c1b25010-caaf-6d0e-4c33-7dae30052840');
        global
            .console
            .log('Indication characteristics' + JSON.stringify(indicationCharacteristics));
        await indicationCharacteristics.startNotifications();
        indicationCharacteristics.addEventListener('characteristicvaluechanged', this.handleBLECharacteristics);
        global
            .console
            .log('notifications started');

        this.valueReading = (await observableCharacteristic(indicationCharacteristics)).map((data) => {
            const decoder = new TextDecoder('utf-8');
            const decodedString = decoder.decode(data);
            return decodedString;
        });

        const notificationCharacteristics = await service.getCharacteristic('c1b25013-caaf-6d0e-4c33-7dae30052840');
        global
            .console
            .log('Notification chararacteristics ' + JSON.stringify(notificationCharacteristics));

        await notificationCharacteristics.startNotifications();
        global
            .console
            .log('Notifications started');
        notificationCharacteristics.addEventListener('characteristicvaluechanged', this.handleBLECharacteristics);

        const characteristic = await service.getCharacteristic('c1b25012-caaf-6d0e-4c33-7dae30052840');

        const enc = new TextEncoder('utf-8');
        characteristic.writeValue(enc.encode('?\r'));

        setInterval(() => {
            if (this.gatt && this.gatt.connected) {
                characteristic.writeValue(enc.encode('?\r'));
            }
        },          1000); 
    }
}


