import * as React from 'react';
import './App.css';

import { MuseClient } from 'muse-js';
import { SylvacClient } from './lib/SylvacClient';

export interface ValueDisplayProps {
  value: number;
}

function ValueDisplay({value}: ValueDisplayProps) {
  return (
    <div>
      got: {value}
    </div>
  );
}

class App extends React.Component {

  public handleSylvacOnClick(event: React.MouseEvent < HTMLButtonElement >): void {
    this.connectSylvac();
  }  

  async connectSylvac() {
    let sylvacClient = new SylvacClient();
    await sylvacClient.connect();
    sylvacClient.valueReading.subscribe((data: string) => { 
      global.console.log(data);
      document.getElementById('sylvacValue')!.innerHTML = data;
    });
  }

  public handleOnClick(event: React.MouseEvent < HTMLButtonElement >): void {
    this.connectMuse();
  }

  async connectMuse() {
    global
      .console
      .log('test');
    let client = new MuseClient();
    await client.connect();
    await client.start();
    client
      .eegReadings
      .subscribe(reading => {
        global.console.log(reading);
      });
    client
      .telemetryData
      .subscribe(telemetry => {
        global.console.log(telemetry);
      });
    client
      .accelerometerData
      .subscribe(acceleration => {
        global.console.log(acceleration);
      });
  }

  render() {
    return (
      <div className="App">
        <button onClick={(e) => this.handleOnClick(e)}>Connect MUSE</button>
        <button onClick={(e) => this.handleSylvacOnClick(e)}>Connect Sylvac</button>
        <p className="App-intro">
          To get started, edit
          <code>src/App.tsx</code>
          and save to reload.
        </p>
        <p id="sylvacValue">xxx</p>
        <ValueDisplay value={333} />
      </div>
    );
  }
}

export default App;



